.button {
  text-decoration: none;
  text-transform: uppercase;
  background-color: @blue;
  color: @white;
  font-size: 21px;
  font-weight: @bold;
  line-height: 26px;
  border: none;
  cursor: pointer;

  &:hover {
    background-color: @blue-hover;
  }

  &:focus {
    background-color: @blue-hover;
  }

  &:active {
    background-color: @blue-active;
    fill: rgba(255, 255, 255, 0.3);
  }
}
