.site-nav {
  position: relative;
}
.site-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
}
@media (max-width: 767px) {
  .site-nav__toggle {
    position: absolute;
    top: 20px;
    transform: translate(0, -50%);
    right: 15px;
    width: 30px;
    height: 30px;
    border: none;
    background-color: #ffffff;
    cursor: pointer;
  }
  .site-nav--closed .site-nav__item {
    display: none;
  }
  .site-nav--closed .site-nav__toggle::before {
    position: absolute;
    content: "";
    top: 10px;
    right: 5px;
    width: 23px;
    height: 4px;
    background-color: #81b3d2;
    box-shadow: 0 10px 0 0 #81b3d2, 0 20px 0 0 #81b3d2;
  }
  .site-nav--closed:hover::before {
    background-color: rgba(129, 179, 210, 0.6);
  }
  .site-nav--closed:active::before {
    background-color: rgba(129, 179, 210, 0.3);
  }
  .site-nav--opened .site-nav__wrapper {
    position: absolute;
    width: 100%;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0);
    z-index: 10;
    min-height: 47vh;
  }
  .site-nav--opened .site-nav__toggle {
    z-index: 11;
    background-color: transparent;
    top: 25px;
  }
  .site-nav--opened .site-nav__toggle::before {
    position: absolute;
    content: "";
    right: 0;
    width: 28px;
    height: 4px;
    background-color: #ffffff;
    transform: rotate(45deg);
  }
  .site-nav--opened .site-nav__toggle::after {
    position: absolute;
    content: "";
    right: 0;
    width: 28px;
    height: 4px;
    background-color: #ffffff;
    transform: rotate(-45deg);
  }
  .site-nav--opened:hover::before {
    background-color: rgba(129, 179, 210, 0.6);
  }
  .site-nav--opened:active::before {
    background-color: rgba(129, 179, 210, 0.3);
  }
  .site-nav--nojs .site-nav__toggle {
    display: none;
  }
  .site-nav--nojs .site-nav__wrapper {
    position: static;
    min-height: 0;
  }
  .site-nav--nojs .site-nav__item {
    display: block;
  }
  .site-nav__item:last-child::before {
    position: absolute;
    content: "";
    bottom: 2px;
    left: 50%;
    transform: translate(-50%, 0);
    width: 320px;
    height: 7px;
    z-index: 13;
    background-image: linear-gradient(to bottom, #81b3d2, rgba(0, 0, 0, 0.2));
  }
  .site-nav__link {
    padding-top: 17px;
    padding-bottom: 13px;
    display: block;
    text-decoration: none;
    text-transform: uppercase;
    color: #ffffff;
    font-size: 18px;
    line-height: 26px;
    font-weight: 800;
    background-color: #81b3d2;
    box-shadow: 0 -1px 0 rgba(1, 1, 1, 0.2) inset, 0 1px 0 rgba(255, 255, 255, 0.2) inset;
  }
  .site-nav__link:hover {
    background-color: #669ec0;
  }
  .site-nav__link:active {
    color: hsla(0, 0%, 100%, 0.3);
    background-color: #5496bd;
  }
  .site-nav__item--empty {
    display: none;
  }
}
@media (min-width: 768px) {
  .site-nav__list {
    min-height: 56px;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .site-nav__toggle {
    display: none;
  }
  .site-nav__item {
    min-width: 153px;
  }
  .site-nav__link {
    padding: 17px 0;
    text-decoration: none;
    text-transform: uppercase;
    color: #000000;
    background-color: #ffffff;
    font-size: 14px;
    font-weight: 800;
  }
  .site-nav__link--active {
    color: #766357;
  }
  .site-nav__link:hover,
  .site-nav__link:active {
    background-color: #ffffff;
  }
}
@media (min-width: 1280px) {
  .site-nav__item {
    min-width: 240px;
  }
}
